.loginpage {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;


    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    /*background-color: #f3f3f3;*/
}

.loginpage .container {
    position: relative;
    width: 400px;
    height: 330px;
}

#loginpage .container .login-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 40px 40px 15px 40px;
    background-color: white;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}

#loginpage .container .login-box .owl {
    position: absolute;
    top: -100px;
    left: 50%;
    margin-left: -111px;
    width: 211px;
    height: 108px;
    background-image: url("./img/owl-login.png");
}

#loginpage .owl .hand {
    position: absolute;
    left: 14px;
    bottom: -8px;
    width: 34px;
    height: 34px;
    border-radius: 40px;
    background-color: #472d20;
    -webkit-transition: .3s ease-out;
    -o-transition: .3s ease-out;
    transition: .3s ease-out;
    -webkit-transform: scaleY(0.6);
        -ms-transform: scaleY(0.6);
            transform: scaleY(0.6);
}

#loginpage .owl .hand.hand-left {
    left: 170px;
}

#loginpage .owl .arms-box {
    position: absolute;
    top: 58px;
    height: 41px;
    width: 100%;
    overflow: hidden;
}

#loginpage .owl .arms {
    position: absolute;
    top: 40px;
    width: 40px;
    height: 65px;
    -webkit-transition: 0.3s ease-out;
    -o-transition: 0.3s ease-out;
    transition: 0.3s ease-out;
    background-image: url("./img/owl-login-arm.png");
}

#loginpage .owl .arms-left {
    left: 158px;
    -webkit-transform: rotate(20deg) scaleX(-1);
        -ms-transform: rotate(20deg) scaleX(-1);
            transform: rotate(20deg) scaleX(-1);
}

#loginpage .owl .arms-right {
    left: 20px;
    -webkit-transform: rotate(-20deg);
        -ms-transform: rotate(-20deg);
            transform: rotate(-20deg);
}

#loginpage .ant-input {

    -webkit-box-shadow: none;

            box-shadow: none;
}

#loginpage .ant-input-group-addon {
    background-color: #6a7989;
    padding: 0;
    border: none;
}

#loginpage .iconfont {
    display: inline-block;
    color: #fff;
    width: 50px;
    -webkit-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
}

#loginpage .ant-form-item-with-help {
    margin-bottom: 24px;
}

#loginpage .ant-form-explain {
    position: absolute;
    z-index: 99;
    left: 110%;
    top: 0;
    height: 40px;
    width: 120px;
    background-color: #fff;
    border: 1px solid #CFCFCF;
    -webkit-box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
            box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.1);
    line-height: 40px;
    padding: 0 15px;
    color: rgba(0, 0, 0, 0.65);
}

#loginpage .registerSubmit .ant-form-explain {
    width: 160px;
}

#loginpage .ant-form-explain:before {
    content: '';
    position: absolute;
    left: -14px;
    top: 12px;
    width: 0;
    height: 0;
    border-right: 14px solid #CFCFCF;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
}

#loginpage .ant-form-explain:after {
    content: '';
    position: absolute;
    left: -13px;
    top: 12px;
    width: 0;
    height: 0;
    border-right: 14px solid #fff;
    border-top: 8px solid transparent;
    border-bottom: 8px solid transparent;
}

#loginpage .container .login-box .bottom {
    padding: 15px 0;
    text-align: right;
}

#loginpage .login-box .bottom .registerBtn {
    color: #999;
}

#loginpage .login-box .bottom .register:hover {
    cursor: pointer;
}

#loginpage .login-box .bottom .gobackBtn {
    float: left;
    color: #999;
    margin-top: 5px;
}




