.registerpage {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-align: center;
        align-items: center;
    margin-bottom: 20px;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 99;
    /*background-color: #f3f3f3;*/
}

.registerpage .container {
    position: relative;
    width: 800px;

}

#registerpage .container .login-box {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    padding: 40px 40px 15px 40px;
    background-color: white;
    -webkit-transition: all 1s;
    -o-transition: all 1s;
    transition: all 1s;
    -webkit-backface-visibility: hidden;
            backface-visibility: hidden;
}


